.call {
  text-align: center;
  margin-top: 40px;
  width: 164px;
  height: 44px;
  background-color: #5671A6;
  border: none;
  border-radius: 50px;
  color: white;
  font-size: 16px;
  font-weight: 600;
  line-height: 24px;
}

.call span {
  cursor: pointer;
  display: inline-block;
  position: relative;
  transition: 0.5s;
}

.call span:after {
  content: url("../../Icons/message.svg");
  position: absolute;
  visibility: hidden;
  transform: scale(0.1);
}

.call:hover {
  background: linear-gradient(104.82deg, #5671A6 11.88%, #B3CBD9 80.84%);
}

.call:hover span {
  padding-right: 45px;
}

.call:hover span:after {
  visibility: visible;
  right: 0;
  top: 3px;
  transform: scale(1);
  transition: transform 0.5s;
}

