.Qualifications {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: start;
  white-space: normal;
}

.Qualifications__header {
  display: flex;
  flex-direction: row;
  align-items: end;
  padding-top: 70px;
  margin-bottom: 15px;
}

.Qualifications__header h1 {
  font-size: 32px;
  line-height: 32px;
  color: #5671A6;
}

.Qualifications__header span {
  margin-left: 15px;
  font-size: 12px;
  line-height: 18px;
  color: #616161;
}

.Tabs__header {
  display: flex;
  flex-direction: row;
}

.Tabs__button {
  display: flex;
  cursor: pointer;
  outline: none;
  height: 100%;
  align-items: end;
  color: #383838;
  margin-right: 126px;
  background: none;
  border: 0;
  font-weight: 600;
  font-size: 16px;
  line-height: 12px;
}
.Tabs__button:hover * {
  color: #5671A6;
  fill: #5671A6;
  transition: all 300ms ease-in-out;
}

.Tabs__icon {
  margin-right: 5px;
}

hr {
  display: block;
  margin: 7px 0 22px 0;
  height: 1px;
  border: none;
  background: #DBDBDB;
  color: #DBDBDB;
}

.Tabs__work, .Tabs__education {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: start;
}

.react-tabs__tab--selected * {
  color: #5671A6;
  fill: #5671A6;
}

@media (max-width: 1000px) {
  .Qualifications {
    margin-bottom: 100px;
  }

  .Tabs__button {
    margin-right: 50px;
  }

  .Tabs__header {
    margin-top: 10px;
  }
}

