*
  padding: 0
  margin: 0

.header
  padding: 17px 25px 16px 14px
  display: flex
  flex-direction: row
  justify-content: space-between
  align-items: center
  color: #5671A6
  border: 1px solid #6D8093
  font-weight: bold

.adaptive__menu *
  fill: #5671A6

.adaptive__menu
  display: none

.logo
  font-size: 36px
  text-decoration: none
  color: #5671A6
  flex-basis: 60%
  cursor: pointer

@media (max-width: 1000px)
  .header
    border: none
    flex-direction: column

  .logo
    font-size: 28px

  .topBlock
    width: 100vw
    display: flex
    flex-direction: row
    justify-content: space-around
    align-items: center

  .adaptive__menu
    display: block








