.AboutCard {
  overflow: hidden;
  min-width: 150px;
  width: 150px;
  height: 150px;
  display: flex;
  flex-direction: row;
  margin: 23px 50px 0 0;
  padding: 14px 10px 14px 10px;
  border-radius: 10px;
  z-index: 1;
}

.AboutCard:hover {
  background: rgba(255, 255, 255, 0.5);
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
}

.AboutCard:hover .AboutCard__title {
  color: #5671A6;
}

.AboutCard:hover .AboutCard__logo * {
  fill: #5671A6;
}

.AboutCard__stat {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  color: #383838;
  /*align-items: flex-end*/
  justify-content: space-between;
}

.AboutCard__title {
  font-weight: bold;
  font-size: 14px;
  line-height: 21px;
  margin-right: 20px;
  z-index: 1;
}

.AboutCard__count {
  font-size: 36px;
  line-height: 36px;
  font-weight: bold;
  margin-top: 10px;
  align-self: end;
}

.AboutCard:hover .AboutCard__count {
  position: relative;
  background: linear-gradient(180deg, #5671A6 0%, #B3CBD9 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  z-index: -1;
  transition: all 0.3s;
}

.oneDigit, .twoDigit {
  font-size: 36px;
  line-height: 36px;
  font-weight: bold;
  margin-top: 10px;
  align-self: end;
  top: 0;
  margin-left: 0;
  transform: rotate(0);
  transition: all 0.5s;
}

.AboutCard:hover .oneDigit {
  font-size: 180px;
  line-height: 180px;
  top: 25px;
  margin-left: -50px;
  transform: rotate(-18.57deg);
}

.AboutCard:hover .twoDigit {
  font-size: 160px;
  line-height: 160px;
  top: -25px;
  margin-left: -90px;
  transform: rotate(-28.34deg);
}

@media (max-width: 400px) {
  .AboutCard {
    margin: 23px 0 0 0;
  }
}

