.JobCard
  display: flex
  flex-direction: column
  min-width: 200px
  width: 200px
  height: 150px
  border: 1px solid #DBDBDB
  box-sizing: border-box
  border-radius: 10px
  padding: 0px 10px
  margin: 0 15px
  &:hover
    background: rgba(255, 255, 255, 0.5)
    box-shadow: 1px 1px 4px rgba(0, 0, 0, 0.25)

.JobCard__title
  word-spacing: 200px
  font-weight: 600
  font-size: 14px
  line-height: 21px
  margin-top: 15px
  transition: all 0.3s

.JobCard:hover .JobCard__title
  font-weight: 600
  font-size: 16px
  line-height: 25px
  color: #5671A6
  transition: all 0.3s
  margin-top: 17px

.JobCard__info
  position: absolute
  margin-top: 91px

.JobCard__location
  font-style: normal
  font-weight: 600
  font-size: 12px
  line-height: 21px

.JobCard__years
  display: flex
  align-items: flex-end
  margin-top: 5px
  font-weight: 400
  font-size: 12px
  line-height: 10px
  color: #616161

.JobCard__icon
  margin-right: 7px

@media (max-width: 1000px)
  .JobCard
    min-width: 150px
    width: 150px
    height: 150px

  .JobCard__location
    font-size: 9px

